  .t-abouts-inner {
    position: relative;
    padding: 0 2rem;
    margin-top: 2rem;
  }

  .t-abouts-heading {
    position: relative;
    font-size: 3rem;
    text-shadow: custome-box-shadow-r(12, 0.25, $secondary-300);

    &::before {
      content: attr(data-title);
      position: absolute;
      left: 50%;
      top: -3.5rem;
      transform: translateX(-50%);
      width: 100%;
      font-size: 4.5rem;
      opacity: 0.2;
    }
  }

  .t-abouts-para {
    font-size: 1.25rem;
    margin-top: 2rem;
    margin-bottom: 3rem;
  }

  .t-abouts-btn {
    padding-left: 3rem;
    padding-right: 3rem;

    @extend %t-index-btn;
  }
}
